<script>
import {mapState} from "vuex";

export default {
  data() {
    return {
      name: '',
      dialog: true,
      loading: false,
    }
  },
  computed: {
    ...mapState(['teachers'])
  },
  mounted() {
    const teacher = this.teachers.find(teacher => teacher.id === Number(this.$route.params.id));
    this.name = teacher.name;
  },
  methods: {
    closeModal() {
      this.dialog = false;
      this.$router.push('/teacher');
    },
    deleteTeacher() {
      this.loading = true;
      this.$store.dispatch('deleteTeacher', {
        id: this.$route.params.id
      }).then(() => {
        this.loading = false;
        this.closeModal();
      })
    }
  }
}
</script>

<template>
  <v-dialog
      v-model="dialog"
      max-width="500px"
  >
    <v-card>
      <v-card-title>
        <span class="headline">Delete Teacher</span>
      </v-card-title>
      <v-card-text>
        <v-form>
          <v-text-field
              label="Name"
              required
              v-model="name"
              disabled
          ></v-text-field>
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
            color="blue darken-1"
            text
            @click="closeModal"
        >Cancel
        </v-btn>
        <v-btn
            color="blue darken-1"
            text
            @click="deleteTeacher"
            :loading="loading"
        >Delete
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

